.home-page {
  text-align: center;
}

.home-page main {
  padding: 20px;
  position: relative;
}

.hero-section {
  position: relative;
  margin-bottom: 20px;
}

.carousel .slide img {
  width: 100%;
  height: 400px; /* Set the height you prefer */
  object-fit: cover;
}

.hero-section h1 {
  font-size: 48px;
  margin-bottom: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.hero-section p {
  font-size: 24px;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.product-categories {
  margin: 40px 0;
}

.product-categories h2 {
  font-size: 32px;
  margin-bottom: 20px;
  text-align: center; /* Ensures consistent alignment */
}

.categories {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.category {
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
  flex: 1;
  max-width: 200px;
  text-align: center;
  cursor: pointer;
}

.product-list {
  margin: 40px 0;
}

.product-list h2 {
  font-size: 32px;
  margin-bottom: 20px;
  text-align: center; /* Ensures consistent alignment */
}

.products {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 20px;
  justify-content: space-evenly;
  align-content: center;
  align-items: center;
  justify-items: center;
}

@media (max-width: 1200px) {
  .products {
      grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 992px) {
  .products {
      grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .products {
      grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .products {
      grid-template-columns: 1fr;
  }
}

.scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: transform 0.3s;
  z-index: 1000;
}

.scroll-to-top img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.scroll-to-top:hover {
  transform: scale(1.1);
}
